<template>
	<view class="container mms-page wechat-page">
		 <view class="search-area">
			 <view class="search-icon"  @click="showDrawer"><uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-sousuo" size="24"></uni-icons></view>
			 <view class="search-info">
			 	<view class="icon-scan" @click="onScan">
			 		<uni-icons custom-prefix="iconfont" color="#ffffff" type="icon-saoyisao" size="28"></uni-icons>
			 	</view>
			 </view>
		 </view>
		<view class="mms-list">
			<view class="section-title">
				<view class="section-title-text">出库单列表</view>
				<view class="title-right-txt">查询结果：{{resListTotal}} 条记录</view>
				<view class="section-title-txt">搜索条件：{{ searchInfo }}</view>
			</view>
			<view class="list" v-if="resList.length > 0">
				<view class="list-item" v-for="item in resList" :key="item">
					<view class="list-item-title"><view class="list-title-icon"><uni-icons custom-prefix="iconfont" color="#fff" type="icon-wuliaoguanli" size="20"></uni-icons></view> {{item.materialsName}}</view>
					<!-- <view class="list-item-detail">详情</view> -->
					<view>物料编号：<view class="list-item-info">{{item.materialsNo}}</view></view>
					<view>规格型号：<view class="list-item-info">{{item.materialsModel}}</view></view>
					<view>库存数量：<view class="list-item-info">{{item.currentInStockNum}} {{item.materialsUnit}}</view></view>
					<view>出存数量：<view class="list-item-info">{{item.outOfStockNum}}  {{item.materialsUnit}}</view></view>
					<view>存放位置：<view class="list-item-info">{{item.storehouseName}}</view></view>
					<view>出库人员：<view class="list-item-info">{{item.operator}}</view></view>
					<view>所属部门：<view class="list-item-info">{{item.dept}}</view></view>
					<view>备注信息：<view class="list-item-info">{{item.remark}}</view></view> 
					<view>出库日期：<view class="list-item-info">{{item.outOfStockTime}}</view></view>
					<view>创建时间：<view class="list-item-info">{{item.createTime}}</view></view>
				</view> 
			</view>
			<view  class="list" v-else>
				<view class="record-nolist">没有可显示的数据</view>
			</view>
		</view>
		<uni-drawer ref="showRight" mode="right" width="260" :mask-click="false">
			<view class="mms-drawer-body">
				<view @click="closeDrawer" class="mms-close" type="primary">x</view>
				<view class="mms-drawer-search">
					<uni-forms :modelValue="searchData">
					        <uni-forms-item name="hobby" label="物料名称">
					            <uni-easyinput type="text" v-model="searchData.materialsName" placeholder="物料名称" />
					        </uni-forms-item>
					        <uni-forms-item name="hobby" label="物料编号">
					            <uni-easyinput type="text" v-model="searchData.materialsNo" placeholder="物料编号" />
					        </uni-forms-item>
					        <uni-forms-item name="hobby" label="规格型号">
					            <uni-easyinput type="text" v-model="searchData.materialsModel" placeholder="规格型号" />
					        </uni-forms-item>
					        <uni-forms-item name="hobby" label="入库日期">
								<uni-datetime-picker
								    type="date"
								    :value="inOrdertime"
								    :end="endTime"
								    @change="onTimeChange"
								/>
					        </uni-forms-item>
					</uni-forms>
					<button class="mms-btn" @click="getRecordList">查 询</button>
				</view>
			</view>
		</uni-drawer>
	</view>
</template>

<script>
	const util = require('../../static/js/util.js'); 
	export default {
		data() {
			return {
				userState: {},
				searchData:{}, 
				searchInfo:'', // 搜索条件文本
				inOrdertime:'', 
				endTime:'',
			    resList:[],
				resListTotal:0
			}
		},
		created(){
			let date = this.dateFormat();
			this.inOrdertime = date;
			this.endTime = date;
			this.searchData.outOfStockTime = date;
			this.userState = util.xcStorage.getStorageSync('userState');
			this.getRecordList();
		},
		methods: {
			// 根据物料编号获取物料信息
			onScan: function() {
				// 只允许通过相机扫码
				var _this = this;
				uni.scanCode({
				    onlyFromCamera: true,
				    success: function (res) {
						let materialsNo = util.xcUrlCode.getMaterialsNo(res.result, 'code');
						_this.searchData = {};
						_this.searchData.materialsNo = materialsNo;
						_this.getRecordList();
				    }
				});
			},
			// 根据物料编号获取物料信息
			getRecordList(){
				var _this = this;
				this.searchInfo = "";
				if(_this.searchData.materialsName){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsName;
				}
				if(_this.searchData.materialsNo){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsNo;
				}
				if(_this.searchData.materialsModel){
					this.searchInfo = this.searchInfo + " " + _this.searchData.materialsModel;
				}
				if(_this.searchData.outOfStockTime){
					this.searchInfo = this.searchInfo + " " + _this.searchData.outOfStockTime;
				}
				
				const url = util.xcRequestURL.WAREHOUSE.getOutHouseRecordList + "?token=" + _this.userState.token;
				util.xcRequest.xcGet(url,_this.searchData, function(res) {
					if(res.status == 200){ 
						let resList = res.result;
						if(resList.length > 0){
							_this.resList = res.result;
							_this.resListTotal = resList.length;
							_this.$refs.showRight.close();
						}else{
							uni.showToast({
								title:'暂无记录！',
								duration:3000 
							});
						}
					}
				});
			},
			showDrawer() {
			    this.$refs.showRight.open();
			},
			closeDrawer() {
			    this.$refs.showRight.close();
			},
			onTimeChange(e){
				this.searchData.outOfStockTime = e;
			},
			dateFormat() {
			  var data = new Date()
			  var year = data.getFullYear();
			  var month = data.getMonth() + 1;
			  var day = data.getDate();
			  // var hours = data.getHours();
			  // var minutes = data.getMinutes();
			  // var seconds = data.getSeconds();
			  
			  month = month >= 10 ? month : '0' + month;
			  day = day >= 10 ? day : '0' + day;
			  var date = year + "-" + month + "-" + day;
			  return date;
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import "../../static/css/style.css";
	.mms-page{
		position:relative;
		height:100%;
		overflow:hidden;
	.section-title-text{
		padding-left:15px;
		font-size:16px;
		border-left:3px solid #FFB800;
	}
	.mms-list{
		height:100%;
	}
	.list-item{
		position:relative;
		margin-bottom:6px;
		padding:12px;
		font-size:14px;
		line-height:1.8;
		color:#888;
		background:#fff;
	}
	.list-item-title{
		padding:2px 5px 2px;
		margin-bottom:5px;
		font-size:15px;
		color:#fff;
		background: rgba(109,59,133,0.9);
		border-bottom:1px solid #efefef;
		border-radius:4px;
	}
	.list-title-icon{
		padding-top:3px;
		padding-right:8px;
		line-height:1;
		vertical-align:top;
		display:inline-block;
	}
}
</style>
